.AddFileBtn {
  margin: 24px 0 0;
  display: flex;
  background: #F5F5F5;
}

.AddFileBtn__input {
  display: none;
}

.AddFileBtn__label {
  border: 1px dashed #D9D9D9;
  border-radius: 2px;
  width: 100%;
  height: 42px;
  display: flex;
  align-items: center;
}

.AddFileBtn__image {
  margin: 0 0 0 52px;
}

.AddFileBtn__text {
  margin: 0 0 0 40px;
  padding: 0;
  color: #595959;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
}

.AddFileBtn__file-name {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #1890FF;
  margin: 0 0 0 10px;
}

.AddFileBtn__file-name_error {
  color: #F5222D;
}

.AddFileBtn__added-file {
  margin: 24px 0 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  position: relative;
}

.AddFileBtn__container {
  display: flex;
}

.AddFileBtn__added-file:hover {
  background: #E6F7FF;
}

.AddFileBtn__added-file.AddFileBtn__added-file_error:hover {
  background: none;
}

.AddFileBtn__add-logo {
  margin: 0 0 0 6px;
}

.AddFileBtn__delete-file {
  margin: 0 7px 0 0;
}

.AddFileBtn__delete-file:hover {
  cursor: pointer;
}

.AddFileBtn__delete-file.AddFileBtn__delete-file_hidden {
  display: none;
}

.AddFileBtn__error-text {
  position: absolute;
  left: 0;
  top: 28px;
  color: #F5222D;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
}

.AddFileBtn__error-text.AddFileBtn__error-text_hidden {
  display: none;
}

@media screen and (min-width: 376px) and (max-width: 740px) {
  .AddFileBtn {
    width: 220px;
  }
}

@media screen and (min-width: 741px) {
  .AddFileBtn {
    margin: 24px 0 0;
    width: 256px;
    height: 44px;
  }

  .AddFileBtn__image {
    margin: 0 0 0 11.5px;
  }

  .AddFileBtn__error-text {
    top: 42px;
  }

  .AddFileBtn__added-file {
    align-items: center;
  }
}
